.section-4 {
	&__inner {
		padding-top: 8vh;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include mh {
			padding-top: 7vh; }
		@include m {
			padding-top: 0;
			margin-bottom: 15px; }
		@include xs {
			flex-wrap: wrap; } }

	&__inner, .nav-slider-wrapper {
		@include mh {
			max-width: 1000px;
			margin: 0 auto; } }
	&__left {
		width: 570px;
		@include mh {
			width: 440px; }
		@include xs {
			margin: 0 auto;
			order: 1; }
		@include r(500) {
			width: 280px!important; } }
	&__right {
		width: calc(100% - 654px);
		@include mh {
			width: calc(100% - 500px); }
		@include m {
			width: calc(100% - 470px); }
		@include xs {
			order: 0;
			flex-basis: 100%; } } }


.slick-slide {
	display: inline-block;
	vertical-align: top; }
.main-slider {
	font-size: 0;
	float: left;
	overflow: hidden;
	width: 100%;
	&__slide {
		height: 490px;
		padding: 2px;
		position: relative;
		display: inline-block;
		vertical-align: top;
		@include mh {
			height: 400px; }
		@include xs {
			height: 280px!important; }
		&:before, &:after {
			content: '';
			position: absolute;
			width: 100px;
			height: 100px;
			background: $red2;
			z-index: 0; }
		&:before {
			right: 0;
			top: 0; }
		&:after {
			bottom: 0;
			left: 0; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			position: relative;
			z-index: 1; } } }
.nav-slider-wrapper {
	text-align: right;
	@include m {
		text-align: left; }
	@include xs {
		text-align: center;
		display: none; } }
.nav-slider {
	position: relative;
	z-index: 1;
	top: -40px;
	display: inline-block;
	vertical-align: top;
	@include sh {
		top: -60px; }
	@include m {
		top: 0!important;
		margin-bottom: 20px; }
	@include xs {
		text-align: center;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 0; }
	&__slide {
		width: 160px!important;
		height: 160px;
		cursor: pointer;
		@include sh {
			width: 90px!important;
			height: 90px; }
		@include lgh {
			width: 130px!important;
			height: 130px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	.nav-slider__slide {
		@include m {
			width: 70px!important;
			height: 70px; } }

	.slick-list {
		padding: 0!important;
		.slick-slide {
			@include m {
				width: 85px!important;
				height: 85px; } } }
	.slick-slide {
		width: 185px!important;
		@include sh {
			width: 105px!important; }
		@include lgh {
			width: 145px!important; } } }
