.section-8 {
	&__inner {
		margin-bottom: 5vh;
		display: flex;
		@include m {
			margin-bottom: 30px; }
		@include xs {
			display: block; } }
	&__left {
		flex-basis: 50%;
		padding-right: 70px;
		text-align: right;
		@include m {
			padding-right: 20px; }
		@include xs {
			padding: 0;
			margin-bottom: 20px;
			text-align: left; } }
	&__right {
		flex-basis: 50%;
		padding-left: 70px;
		@include m {
			padding-left: 20px; }
		@include xs {
			padding: 0; }
		p {
			font-size: 20px;
			margin-bottom: 20px;
			@include sh {
				font-size: 16px;
				margin-bottom: 15px; }
			@include m {
				margin-bottom: 10px; } } }
	.title-m {
		margin-bottom: 7vh;
		@include mh {
			margin-bottom: 3vh; } }
	.justify-content {
		max-width: 390px;
		align-items: center;
		margin-left: auto;
		margin-bottom: 20px;
		@include sh {
			margin-bottom: 10px;
			max-width: 300px; }
		@include xs {
			margin: 0 0 10px; }
		p {
			font-size: 24px;
			@include sh {
				font-size: 20px; }
			@include m {
				font-size: 16px; } } }
	.decor-block {
		&__inner {
			height: 25vh; } }
	.l {
		height: auto; } }
