.section-9 {
	&__inner {
		display: flex;
		font-size: 0;
		@include sm {
			display: block; } }
	&__left {
		flex-basis: 50%;
		padding-right: 15px;
		text-align: right;
		@include sm {
			padding-right: 0; } }
	&__right {
		flex-basis: 50%;
		padding-left: 15px;
		@include sm {
			padding-left: 0; } }
	&__after-title {
		font-size: 24px;
		text-align: center;
		margin-bottom: 4vh;
		@include sm {
			font-size: 16px;
			margin-bottom: 30px; } }

	.l {
		height: auto;
		@include sh {
			max-width: 940px; } }
	.title-m {
		margin-bottom: 1vh;
		@include m {
			margin-bottom: 10px; } } }
.field {
	border: 1px solid #fcbe95;
	background: $white;
	margin-bottom: 30px;
	@include dh {
		margin-bottom: 20px; }
	@include sh {
		margin-bottom: 10px; }
	@include trans;
	&.is-error {
		border-color: $red2; }
	&--textarea {
		textarea {
			height: 138px;
			@include dh {
				height: 110px; }
			@include mh {
				height: 94px; }
			@include sh {
				height: 86px; } } }

	input, textarea {
		color: $gray;
		padding: 18px 18px;
		@include rob;
		display: block;
		width: 100%;
		border: none;
		text-align: left;
		font-size: 14px;
		@include dh {
			padding:  14px 14px; }
		@include mh {
			padding:  10px 14px; }
		@include sm {
			padding: 6px;
			font-size: 12px; } }
	textarea {
		resize: none; } }
.btn-submit {
	display: inline-block;
	vertical-align: top;
	color: #fdfdfd;
	@include rob;
	font-size: 24px;
	background-color: $red2;
	border: 1px solid $red3;
	padding: 13px 15px;
	min-width: 262px;
	text-align: center;
	@include trans;
	@include mh {
		padding: 8px 15px;
		min-width: 200px;
		font-size: 20px; }
	&:hover {
		background: $white;
		color: $red2; } }
