@mixin rob {
	font-family: 'Roboto', sans-serif; }
@include font(Rosamunda, Rosamunda-One);
@mixin roca {
	font-family: Rosamunda, sans-serif; }

$white: #fff;
$black: #000;
$red: #df0327;
$red2: #e92701;
$red3: #fcbe95;
$red5: #f7c3a1;
$purple: #651960;
$gray: #242424;


body {
	background: $white;
	line-height: 1.4;
	@include font-smoothing;
	@include rob;
	font-size: 2vh;
	overflow: hidden;
	@include m {
		overflow: visible;
		font-size: 16px; } }

a {
	text-decoration: none; }
h1,h2,h3,h4,h5,h6 {
	font-weight: normal; }
.title-lg, .title-m, .title-sm {
	color: #df0327;
	@include roca; }

.title-lg {
	font-size: 10vh;
	@include sm {
		font-size: 40px; } }
.title-m {
	font-size: 60px;
	margin-bottom: 45px;
	text-align: center;
	@include sm {
		font-size: 34px;
		margin-bottom: 20px; } }
.title-sm {
	font-size: 40px;
	@include sh {
		font-size: 28px; }
	@include m {
		font-size: 24px;
		line-height: 1; } }
.decor-text {
	color: $purple;
	// font-size: 10vh
	font-size: 30px;
	text-align: center;
	@include m {
		display: none; }
	&--half-cirle {
		min-width: 540px;
		height: 170px;
		line-height: 170px;
		@include lg {
			font-size: 18px;
			height: 100px;
			line-height: 100px;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: 50% 100%; }
		.top-lines {
			top: 49%;
			// +m
			// 	display: none
			&:before {
				left: 65px;
				@include lg {
					left: 120px; } }
			&:after {
				right: 65px;
				@include lg {
					right: 120px; } } } } }
.top-lines {
	position: absolute;
	z-index: 0;
	top: 30%;
	width: 100%;
	@include m {
		display: none; }
	&:before, &:after {
		content: '';
		position: absolute;

		width: 4000px;
		height: 1px;
		background: $red5; }
	&:before {
		left: 0;
		transform: translateX(-100%); }
	&:after {
		right: 0;
		transform: translateX(100%); }
	&--red {
		&:before, &:after {
			background: $red2; } }
	&--full {
		&:before {
			left: 50%; }
		&:after {
			right: 50%; } } }
.two-col {
	height: 100%;
	@include clr;
	&__left {
		float: left;
		height: 100%;
		@include sm {
			float: none; } }
	&__right {
		float: right;
		height: 100%;
		@include sm {
			float: none; } } }
.article {
	color: $gray;
	font-size: 3vh;
	@include sm {
		font-size: 16px; }
	p {
		margin-bottom: 2vh;
		@include sm {
			margin-bottom: 15px; } } }

.decor-block {
	border-top: 2px solid $red2;
	border-bottom: 2px solid $red2;
	padding: 9px 0;
	&__inner {
		background-position: 50% 50%;
		background-size: cover;
		background-repeat: no-repeat; } }
.why-block {
	max-width: 490px;
	@include m {
		max-width: 100%; }
	.title-m {
		text-align: center; } }
.quote {
	position: relative;
	color: $gray;
	font-size: 24px;
	font-weight: 300;
	padding-left: 57px;
	@include sm {
		font-size: 16px; }
	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		@include s(quote-img); } }
.decor-vert {
	color: $gray;
	font-size: 24px;
	font-weight: 300;
	position: relative;
	margin-bottom: 30px;
	@include lg {
		font-size: 3vh; }
	@include m {
		font-size: 16px; }
	@include xs {
		margin-bottom: 50px;
		padding: 10px 0; }
	&:before, &:after {
		content: '';
		position: absolute;
		left: 0;
		width: 68px;
		height: 1px;
		background-color: $red2;
		@include xs {
			left: 50%;
			transform: translateX(-50%); } }
	&:before {
		top: -20px;
		@include xs {
			top: 0; } }
	&:after {
		bottom: -20px;
		@include xs {
			bottom: 0; } } }
.justify-content {
	display: flex;
	align-items: flex-start;
	justify-content: space-between; }
.link-under {
	color: $gray;
	font-size: 20px;
	@include trans;
	display: inline-block;
	vertical-align: top;
	position: relative;
	@include m {
		font-size: 16px; }
	&:after {
		@include rmin(1024) {
			content: '';
			position: absolute;
			width: 100%;
			bottom: 0px;
			height: 1px;
			background: $gray;
			left: 0;
			@include trans; } }
	&:hover {
		@include rmin(1024) {
			color: $red2;
			&:after {
				opacity: 0; } } } }
.centered {
	text-align: center; }

